.skills-container {
    padding: 5% 10%;
    display: flex;
    flex-direction: column;
    text-align: left;

    svg {
        color: white;
        font-size: 2em;  
        transition: transform 0.3s ease, color 0.3s ease;  

        &:hover {
            transform: scale(1.1) translateY(-5px);  
            color: #ff4d29;  
        }
    }
}

.light-mode .skills-container svg {
    color: black; 
    &:hover {
        transform: scale(1.1) translateY(-5px);  
        color: #ff4d29;  
    }
}



  
.skills-container .skills-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 50px;
}
  
.skills-container .skills-grid .skill {
    align-items: center;
    text-align: left;
}
  
.chip {
    color: rgb(39, 40, 34) !important;
    background-color: white !important;
    font-size: 0.8rem !important;
    height: 25px !important;
  
    span {
        font-family: 'Courier Prime', monospace;
        color: rgb(39, 40, 34);
    }
  
    svg {
        height: 15px !important;
    }
}

.flex-chips {
    line-height: 2;
    margin: 0.5;

    .chip-title {
        color: #ff4d29;
        padding-right: 10px;
    }

    .chip {
        background-color: #e0e0e0;
        font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
        transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease;
    }

    .chip:hover {
        background-color: #ff4d29 !important;  
        color: white;  
        transform: scale(1.1);  
    }

    .chip .MuiChip-label {
        color: rgb(39, 40, 34);  
    }
}

.flex-chips .MuiChip-root {
    margin-right: 0.5em;
}

.flex-chips .MuiChip-outlined {
    background-color: #e0e0e0;
    
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

.flex-chips .MuiChip-outlined span {
    color: rgb(39, 40, 34);
}

@media screen {
    @media (max-width: 768px) {
        .skills-container {
            display: block;
            padding-left: 5%;
            padding-right: 5%;
        }
    
        .skills-container .skills-grid {
            display: initial;
        }
        
        .skills-container .skills-grid .skill {
            padding-bottom: 5%;
        }
    }

    @media (max-width: 567px) {
        .skills {
            font-size: 12px;
        }
    }
}