.dark-mode .about-section {
    background-color: rgba(0, 0, 0, 0.2);
}

.light-mode .about-section {

background-image:  url("../images/cool-background.svg");;
}

.light-mode .about-section .content .social_icons svg:hover {
    transform: scale(1.1) translateY(-5px); 
    box-shadow: none;
    fill: #ff4d29;  
}

.light-mode .about-section .content .social_icons svg {
    border-radius: 50%;  
}

@font-face {
    font-family: 'MIXOLYDIAN TITLING';
    src: url('/assets/mixolydian_titling/Mixolydian Titling Rg It.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
  }
  

.about-section {
    display: flex;
    gap: 30px;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    padding: 0px 15%;
    min-height: 700px;
    background-image: url("../images/popbg1_04.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
}

.about-section .content {
    text-align: left;
    z-index: 2;

    .social_icons {
        display: flex;
        flex-direction: row;
        gap: 10px;
        color: white;
    
        svg {
            font-size: 1.8em;
            transition: transform 0.3s ease, box-shadow 0.3s ease;  
    
            &:hover {
                transform: scale(1.1) translateY(-5px); 
                box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);  
            }
        }
    }
    

    .mobile_social_icons {
        display: none;
    }

    p {
        font-size: 1.5em;
        margin-block-start: 0px;
        margin-block-end: 0px;
        transition: color 0.3s ease; 
    }
    
    p:hover {
        color: #ff4d29; 
        transform: scale(1.1) translateY(7px); 
        cursor: pointer; 
    }
    

    h1 {
        font-size: 4.7em;
        margin-block-start: 0px;
        margin-block-end: 0px;
        font-family: 'MIXOLYDIAN TITLING', sans-serif;

    }
    h3{
        position: relative;
        font-size: 40px;  
        top: 60px;

    }
    
}

.about-section .image-wrapper {
    z-index: 2; 
    overflow: visible;
  
    img {
      width: 187px;
      border-radius: 50%;
      transition: transform 0.3s ease, box-shadow 0.3s ease;  
      &:hover {
        transform: scale(1.1) translateY(-5px);  
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);  
      }
    }
  }

  
  
  

.about p {
    text-align: center;
    padding-top: 30%;
    padding-bottom: 30%;
}

@media screen {
    @media (max-width: 567px) {
        .nav-left{
            padding-top: 20px;
            float:left;
        }

        .nav-left img {
            height: 35px
        }

        .nav-right {
            float:right;
        }

        .nav-right li {
            padding: 10px 15px;
            list-style:none;
            font-size:13px;
            color:white;
            width: 10%;
        }

        .nav-right ul {
            list-style-type: none;
            margin: 0px;
            padding: 0px;
        }

        body {
            width: 100%;
        }

        .about {
            padding-left: 5%;
            padding-right: 5%;
            font-size: 12px;
        }
    }

    @media (max-width:768px) {
        .about-section h1 {
            font-size: 4em !important;
            text-align: left;
        }

        .about-section p {
            text-align: left;
        }

        .container {
            display: initial;
        }

        .social_icons {
            display: none !important;
        }

        .mobile_social_icons {
            padding-top: 10px;
            display: flex !important;
            flex-direction: row;
            gap: 10px;
            color: white;
    
            svg {
                font-size: 1.8em;
            }

            h1 {
                position: relative;
                top: 150px;  
                margin-top: 20px; 
            }
        }

        .about-section {
            padding-top: 150px;
            display: block;
            padding-left: 0px;
            padding-right: 0px;
        }

        .about-section .content {
            width: 90%;
            padding-left: 5%;
            padding-right: 5%;
            padding-bottom: 5%;
        }

        .about-section .image-wrapper {
            padding-left: 5%;
            width: 100%;
        }
    }
}