.vertical-timeline span {
    color: white;
    font-family: 'Courier Prime', monospace;
  }
  
  .vertical-timeline p {
    color: rgb(39, 40, 34);
  }
  
  .vertical-timeline-element p {
    color: rgb(39, 40, 34);
  }
  
  .vertical-timeline-element h3 {
    color: rgb(39, 40, 34);
  }
  
  .vertical-timeline-element h4 {
    color: rgb(39, 40, 34);
  }
  
  .vertical-timeline-element span {
    color: rgb(39, 40, 34);
  }
  
  .vertical-timeline-element .vertical-timeline-element-date {
    color: white;
  }
  
.light-mode .vertical-timeline-element .vertical-timeline-element-date {
  color: black impo !important;
}

  .svg-inline--fa {
    color: white;
  }
  
  .light-mode .vertical-timeline-element-divider {
    border-right: 2px solid rgb(39, 40, 34) !important;
    border-left: none !important;
  }
  
  
  
 
  
  @media screen and (max-width: 1170px) {
    .vertical-timeline-element .vertical-timeline-element-date {
      color: rgb(39, 40, 34);
    }
  }
  
  
  .vertical-timeline-element-content {
    transition: all 0.3s ease-in-out;
  }
  
  .vertical-timeline-element-content:hover {
    background-color: #ff4d29 !important; 
    transform: scale(1.05) !important; 
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2) !important
  }
  