.light-mode svg:hover {

    color: #ff4d29 !important;
    transform: scale(1.1)!important; 

}


.light-mode .thm-logo {
    filter: invert(100%) !important;
  }
  .light-mode .thm-logo:hover {
    filter: invert(47%) sepia(100%) saturate(900%) hue-rotate(4deg) brightness(80%) contrast(120%) !important;

  }

footer {
    text-align: center;
    padding-top: 50px;
    padding-bottom: 50px;
  
    svg {
      width: 1.1em !important;
      height: 1.1em !important;
      margin-right: 10px;
    }
    
    .footer-thm-link {
      text-decoration: none;
      display: inline-block;
    }
  
    .thm-logo {
      width: 30px;  
      height: auto;
      margin-left: 10px;
      vertical-align: middle;
      transition: transform 0.3s ease-in-out;   
    }
  
    .thm-logo:hover {
        filter: invert(47%) sepia(100%) saturate(900%) hue-rotate(4deg) brightness(80%) contrast(120%) !important;
      }
  
    p {
      font-size: 0.8em;
    }
  }


  .rating-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
  
    p {
      margin-right: 10px;
      font-size: 1rem;
      font-weight: bold;
    }
  
    span {
      cursor: pointer;
      font-size: 24px;
      transition: transform 0.2s ease-in-out, color 0.2s ease-in-out;
  
      &:hover {
        transform: scale(1.3);
        color: #ffcc00 !important; 
      }
  
      &:active {
        transform: scale(1.1);
      }
    }
  }
  