
.light-mode .contact-title {
  color: black !important;
}


.light-mode .contact-description {
  color: black !important;

}

#contact {
  padding: 4rem 2rem;
  
  text-align: center;

  .contact-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 2rem;
    
    transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.5s ease;
  
    &:hover {
      transform: translateY(-10px) scale(1.05) rotate(-1deg);
      box-shadow: 0 15px 20px #ff4d29;
    }
  }
  

  .contact-title {
    font-size: 2.5rem;
    font-weight: 700;
    color: whitesmoke;
    margin-bottom: 1rem;
  }

  .contact-description {
    font-size: 1.2rem;
    color: whitesmoke;
    margin-bottom: 2rem;
  }

  

  .contact-links {
    display: flex;
    justify-content: center;
    gap: 2rem;

    .contact-link {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      font-size: 1.1rem;
      font-weight: 500;
      text-decoration: none;
      color: #0073b1;
      transition: color 0.3s ease;

      &:hover {
        color: #ff4d29;
      }

      svg {
        color: inherit;
      }
    }
  }
}
